@font-face {
  font-family: 'Sacramento';
  src: url(./fonts/Sacramento-Regular.ttf);
}

.App {
  text-align: center;
  font-size: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.row{
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
}

.colored-text{
  font-family: 'Sacramento';
  margin: 1rem 0 0;
  font-size: 4rem;
  line-height: 1;
  color: rgb(117, 218, 255);
  text-shadow: 0.2rem 0.35rem 0.3rem rgb(0, 0, 0);
}

button{
  background-color:rgb(192, 0, 61);
  text-align: center;
  font-size: 1.5rem;
  margin: 0.4rem 0 0 1.3rem;
  height: 2.5rem;
  width: 2.5rem;
  color: rgb(130, 240, 255);
  text-shadow: 0rem 0.1rem 0.2rem rgb(0, 0, 0);
  border-radius: 0.4rem;
  border: 0.15rem outset rgba(255, 255, 255, 0.726);
  -webkit-box-shadow: 0.1rem 0.3rem 0.4rem rgb(0, 0, 0);
          box-shadow: 0.1rem 0.3rem 0.4rem rgb(0, 0, 0);
}

.square{
  padding-top: 0.5rem;
  color: rgb(85, 0, 50);
  font-size: 3rem;
  display: -ms-flexbox;
  display: flex;
  background-color: rgb(54, 223, 238);
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 4rem;
  width: 4.3rem;
  margin: 0.5rem;
  border: 0.4rem outset rgb(132, 26, 86);
  border-radius: 0.5rem;
  -webkit-box-shadow: 0.2rem 0.4rem 0.5rem rgb(0, 0, 0);
          box-shadow: 0.2rem 0.4rem 0.5rem rgb(0, 0, 0);
}

p{
  color: rgb(126, 156, 156);
  max-width: 300px;
}
a{
  color: rgb(106, 206, 206);
  text-decoration: none;
}


